import React from "react";
import { compose, withStateHandlers } from "recompose";
import playButtonImage from "assets/img/vimeo-play-button.svg";

import {
  IFrame,
  Wrapper,
  PlaceholderImage,
  PlaceholderCaption,
  PlayButton,
} from "./elements";

const Video = ({
  videoLink,
  placeholderImage,
  placeholderImageSrcSet,
  placeholderImageSizes,
  placeholderCaption,
  playingVideo,
  toggleVideoPlaying,
  id,
  captionPosition,
}) => (
  <Wrapper id={id}>
    {playingVideo ? (
      <IFrame
        src={`${videoLink}?rel=0&autoplay=1`}
        frameBorder="0"
        webkitallowfullscreen
        mozallowfullscreen
        allowFullScreen
        allow="autoplay; encrypted-media"
      />
    ) : (
      <React.Fragment>
        <PlaceholderImage
          src={placeholderImage}
          srcSet={placeholderImageSrcSet}
          sizes={placeholderImageSizes}
          onClick={toggleVideoPlaying}
        />
        <PlayButton src={playButtonImage} onClick={toggleVideoPlaying} />
        <PlaceholderCaption captionPosition={captionPosition}>
          {placeholderCaption}
        </PlaceholderCaption>
      </React.Fragment>
    )}
  </Wrapper>
);

export default compose(
  withStateHandlers(
    () => ({
      playingVideo: false,
    }),
    {
      toggleVideoPlaying: () => ({ playingVideo }) => ({
        playingVideo: !playingVideo,
      }),
    },
  ),
)(Video);
