import React from "react";
import { compose, withStateHandlers, lifecycle } from "recompose";
import isPropValid from "@emotion/is-prop-valid";
import LazyLoad from "react-lazyload";

const getValidHTMLProps = (props) =>
  Object.keys(props).reduce(
    (acc, prop) => ({
      ...acc,
      ...(isPropValid(prop) && { [prop]: props[prop] }),
    }),
    {},
  );

// eslint-disable-next-line
const _DisabledMobileVideo = (props) => {
  const { source, videoFirstFrame, placeholderImage } = props;
  return (
    <LazyLoad
      offset={100}
      once
      placeholder={<img style={{ width: "100%" }} src={placeholderImage} />}
    >
      <video
        {...getValidHTMLProps(props)}
        src={source}
        poster={videoFirstFrame || placeholderImage}
      />
    </LazyLoad>
  );
};

const _lifecycle = {
  componentDidMount() {
    if (window.innerWidth > 576 || this.props.allowVideoOnSmallScreen) {
      this.props.updateSource();
    }
  },
};

export default compose(
  withStateHandlers(
    {
      source: "",
      videoFirstFrame: null,
    },
    {
      updateSource: (state, props) => () => ({
        source: props.dataSrc,
        videoFirstFrame: props.videoFirstFrame,
      }),
    },
  ),
  lifecycle(_lifecycle),
)(_DisabledMobileVideo);
