import React from "react";
import { withStateHandlers } from "recompose";

import { Container, TextWrapper, Icon, Title, Text } from "./elements";

const MosaicValueProp = ({
  active,
  setActive,
  setUnactive,
  iconGray,
  iconWhite,
  title,
  body,
}) => (
  <Container
    active={active}
    onMouseEnter={setActive}
    onMouseLeave={setUnactive}
  >
    <TextWrapper active={active}>
      <Title active={active}>{title}</Title>
      <Text active={active}>{body}</Text>
    </TextWrapper>
    <Icon src={active ? iconWhite : iconGray} />
  </Container>
);

export default withStateHandlers(
  { active: false },
  {
    setActive: () => () => ({
      active: true,
    }),
    setUnactive: () => () => ({
      active: false,
    }),
  },
)(MosaicValueProp);
