export const BreakdownRow = styled("div")(
  ({ isTopRow = false, isBottomRow = false }) => [
    {
      display: "flex",
      // alignItems: "flex-end",
    },
    isTopRow && {
      alignItems: "flex-end",
    },
    isBottomRow && {
      alignItems: "flex-start",
    },
  ],
);

const regularPadding = 20;
const extraPadding = 20;
const minHeight = 140;

export const BreakdownCell = styled("div")(
  ({
    isHeader = false,
    isLabel = false,
    isHighlighted = false,
    isExtendedTop = false,
    isExtendedBottom = false,
    index = false,
  }) => [
    {
      display: "flex",
      width: "22.5%",
      background: "#000",
      color: "#fff",
      alignItems: "center",
      justifyContent: "center",
      textAlign: "center",
      fontSize: 14,
      ...padding(regularPadding),
    },
    isHeader && {
      minHeight,
      fontWeight: 600,
      color: isHighlighted ? "#fff" : "#e9794b",
      fontSize: 32,
    },
    {
      borderBottom: !(isHeader || isHighlighted)
        ? "1px solid #232526"
        : "1px solid transparent",
    },
    isHighlighted && {
      background: "#e9794b",
    },
    isExtendedTop && {
      minHeight: minHeight + extraPadding,
      paddingTop: regularPadding + extraPadding,
    },
    isExtendedBottom && {
      paddingBottom: regularPadding + extraPadding,
      boxShadow: "0 3px 5px rgba(0, 0, 0, .1)",
    },
    index && {
      width: "10%",
      borderBottom: "1px solid transparent",
      justifyContent: "flex-end",
      color: "#383839",
      fontWeight: 600,
      fontSize: 12,
    },
    isLabel && {
      color: "#e9794b",
      fontWeight: 600,
      justifyContent: "flex-start",
      fontSize: 12,
      textTransform: "uppercase",
    },
  ],
);

